import { useState, useEffect } from 'react';



export default function useCountries() {

    const [onlyCountryNamesArray, setOnlyCountryNamesArray] = useState([]);

    useEffect(() => {
        setOnlyCountryNamesArray([])
        countryArray.forEach(country =>
            setOnlyCountryNamesArray(oldArray => [...oldArray, country["label"]])
        )
    }
        , [])


    return onlyCountryNamesArray;
}

