    userRule2: ` Yarışma süresince görüntülerinin tespit edilmesine muvafakat ettiğini, tespit edilecek görüntülerine, Fotoğraflarına ilişkin olarak FSEK madde 80/1-A uyarınca icracı sanatçı sıfatı ile sahip olduğu icrasının tespit edilmesi, bu tespitin çoğaltılması, satılması, dağıtılması, kiralanması ve ödünç verilmesi; işaret, ses ve/veya görüntü nakline yarayan araçlarla umuma iletimi ve yeniden iletimi; temsili; aslı veya çoğaltılmış nüshalarının satış yoluyla veya diğer yollarla dağıtılması; telli veya telsiz araçlarla satışı veya diğer biçimlerde umuma dağıtımı, sunulması, teşhiri ve gerçek kişilerin seçtikleri yer ve zamanda icraya ulaşılmasını sağlamak suretiyle umuma iletimi adı altında düzenlenen tüm komşu haklarımı yer, sayı, süre, mecra ve içerik itibariyle sınırsız olarak, tüm bu hakları yurt içinde ve yurt dışında istediği şartlarda, gerçek ve tüzel kişilere lisans verme/devretme hakları dahil olmak üzere beautyfashionfactory.com’a gayri kabili rücu devredeceğini;`,
